import Swiper from 'swiper/bundle';
import 'swiper/css/bundle';

document.addEventListener("DOMContentLoaded", function () {
    const therapySwiper = new Swiper('.therapy-swiper', {
        slidesPerView: "auto",
        spaceBetween: 30,
        observer: true,
        autoplay: false,

        pagination: {
            el: ".swiper-pagination",
            clickable: true,
        },

        navigation: {
            nextEl: '.swiper-button-next',
            prevEl: '.swiper-button-prev',
        },
    });
});

// popup
document.addEventListener("DOMContentLoaded", function () {
    // Pobieramy wszystkie ikony pytania
    const questionIcons = document.querySelectorAll('.question_icon');

    questionIcons.forEach(function(icon) {
        icon.addEventListener('click', function() {
            // Znajdujemy najbliszego rodzica z klas .activity-info
            const activityInfo = icon.closest('.activity-info');
            if (activityInfo) {
                // Szukamy w obrbie tej aktywnoci elementu popup (bez wzgldu na to, czy ma klas closed czy nie)
                const popup = activityInfo.querySelector('.popup_desc_fz');
                if (popup) {
                    popup.classList.remove('closed');
                }
            }
        });
    });

    // Pobieramy wszystkie przyciski zamknicia we wszystkich popupach
    const closeButtons = document.querySelectorAll('.popup_desc_fz__close');

    closeButtons.forEach(function(btn) {
        btn.addEventListener('click', function() {
            // Znajdujemy najbliszy popup (rodzica o klasie .popup_desc_fz)
            const popup = btn.closest('.popup_desc_fz');
            if (popup) {
                popup.classList.add('closed');
            }
        });
    });
});
